import React, { Component } from "react";
import Jumbotron from "./Jumbotron";
import SearchForm from "./SearchForm";
import ResultList from "./ResultList";
import API from "../utils/API";

class EmployeeContainer extends Component {
  
  state = {
    search: "",
    results: [],
    copyResults: [],
  };

   componentDidMount() {
    this.searchEmployees("api");
  }

  searchEmployees = query => {
    API.search(query)
      .then(res => this.setState({ results: res.data.results, copyResults: res.data.results}))
      // .then(res => this.setState({ results: res.data.results, copyResults:res.data.results },
      // () => console.log(this.state.results)))  w/ console.log
      .catch(err => console.log(err));
  };

  // gets name and value from SearchForm
  handleInputChange = event => {
    const name = event.target.name;
    const value = event.target.value;
    this.setState({
      [name]: value  //search:value
    });
    
    // filters to compare the value is in either first or last name
    const employee = this.state.copyResults.filter(employee => {
      return employee.name.first.toLowerCase().includes(value.toLowerCase())  ||
      employee.name.last.toLowerCase().includes(value.toLowerCase())
    })
    this.setState({results: employee})
  };

  sortName = event => {
    if (this.state.results === this.state.copyResults) {
    // https://stackoverflow.com/questions/1129216/sort-array-of-objects-by-string-property-value
    // https://www.w3schools.com/jsref/jsref_localecompare.asp
      // const sort =  this.state.copyResults.sort((a, b) => a.name.last.localeCompare(b.name.last));
      // copyResults is rewritten when using localCompare
      const sort = [...this.state.copyResults]
      sort.sort((a, b) => a.name.last.localeCompare(b.name.last))
      // sort creates a copy of the copy and then sorts
      this.setState({results: sort})
    } 
    else {
      const unsort = this.state.copyResults
      this.setState({results: unsort})
    }
 
  };

  render() {
    return (
      <div>
        <Jumbotron />
        <SearchForm
          search={this.state.search}
          handleInputChange={this.handleInputChange}
        />
        <ResultList 
          results={this.state.results} 
          sortName={this.sortName}
          />
      </div>
    );
  }
}

export default EmployeeContainer;