import React from "react";
import "./style.css";
// https://create-react-app.dev/docs/adding-images-fonts-and-files/
import sortIcon from "./sort.png"

// https://www.youtube.com/watch?v=j5P9FHiBVNo for inline
const tableStyle = {
  textAlign: 'center',
};

const rowStyle = {
  justifyContent: 'center',
  marginTop: '10px'
};

// https://stackoverflow.com/questions/11497094/remove-border-from-buttons/11497126
const buttonStyle = {
  border: 'none',
  background: 'none'
};

const filterIcon = {
    marginLeft: '10px',
    height: '12px',
    width: '12px'
};

function ResultList(props) {
  // console.log(props)
  // console.log(props.results)
  // https://dev.to/abdulbasit313/an-easy-way-to-create-a-customize-dynamic-table-in-react-js-3igg
  // https://mdbootstrap.com/docs/react/utilities/vertical-align/
  return (
    <div className="container">
      
      <table className="table table-striped" style={tableStyle}>
        <thead>
          <tr>
            <th scope="col">Image</th>
            <th scope="col">
              <button className="sort" style={buttonStyle} onClick={props.sortName}>
                Name
                <img src={sortIcon} style={filterIcon} alt="sort"></img>
              </button>
            </th>
            <th scope="col">Phone</th>
            <th scope="col">Email</th>
            <th scope="col">DOB</th>
          </tr>
        </thead>
        <tbody>
          {props.results.map(result => (
              <tr key={result.id.value} style={rowStyle}>
                <img alt={result.name.first} className="img-fluid" src={result.picture.medium} />
                <td className="align-middle">{result.name.first} {result.name.last}</td>
                <td className="align-middle">{result.phone}</td>
                <td className="align-middle">{result.email}</td>
                <td className="align-middle">{result.dob.date.slice(5, 10)}-{result.dob.date.slice(0, 4)}</td>
              </tr>
            ))}
        </tbody>
      </table>
    </div>
  );
}

export default ResultList;
